body {
  margin: 0;
  padding: 0;
}

.stars {
  margin: 0;
  padding: 0;
  display: flex;

  /* margin-left: -2px; */
}

.stars__star {
  padding: 0;
  margin: 0;
  width: 16px;
  height: 16px;

  background-repeat: no-repeat;
  background-image: url(images/star.svg);
  background-position: center;
}

.stars__star:not(:last-child){
  margin-right: 4px;
}

